.form { display: flex; flex-direction: column; justify-content: space-between; max-width: 665px; width: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding: 27px 30px 32px; border: 3px solid #c7a221; border-radius: 5px; }

@media only screen and (max-width: 1500px) { .form { padding: 21px 26px 32px; } }

.form__title { margin-bottom: 24px; text-align: center; text-shadow: 0 3px 11px #000; color: #fff; font-family: "Circle"; font-size: 53px; font-weight: 700; line-height: 1; letter-spacing: -0.02em; word-spacing: 0.043em; }

