@media only screen and (max-width: 599px) { .form__input-group { margin-bottom: 16px; } }

.form__input-group .help-block.form-error { position: absolute; top: 100%; left: 2px; width: 100%; text-shadow: 0 3px 11px #000; color: #fff; font-family: "Circle"; font-size: 18px; font-weight: 400; line-height: 1; }

@media only screen and (max-width: 1500px) { .form__input-group .help-block.form-error { font-size: 16px; } }

