.input { padding: 22px 10px 17px; width: 100%; color: #000; font-family: "Circle"; font-size: 25px; font-weight: 300; line-height: 1.5677; border: 3px solid transparent; height: 64px; background-color: #fff; transition: border-color 0.3s; }

@media only screen and (max-width: 1500px) { .input { height: 56px; font-size: 22px; } }

@media only screen and (max-width: 599px) { .input { height: 30px; font-size: 18px; } }

.input:focus { border-color: #14b737; }

.input.error { border-color: #ff0000 !important; }

.input:placeholder { color: #000; font-family: "Circle"; font-size: 22px; font-weight: 300; line-height: 1.5677; }

.checkbox { display: flex; align-items: center; cursor: pointer; }

.checkbox__input { position: absolute; top: 0; height: 0; width: 0; opacity: 0; visibility: hidden; }

.checkbox__input:checked ~ .checkbox__icon:before { transform: scale(1) rotate(45deg) translate(-1px, -1px); }

.checkbox__text { display: inline-block; max-width: 390px; padding-left: 32px; text-shadow: 0 3px 11px #000000; color: #fff; font-size: 24px; font-weight: 400; line-height: 1.2; position: relative; text-align: center; }

@media only screen and (max-width: 1023px) { .checkbox__text { max-width: 350px; padding-left: 24px; font-size: 21px; } }

@media only screen and (max-width: 599px) { .checkbox__text { font-size: 12px; padding-left: 6px; text-align: left; } }

.checkbox__text a { color: inherit; text-decoration: none; }

.checkbox__icon { display: inline-block; flex-shrink: 0; position: relative; width: 44px; height: 44px; border-radius: 3px; border: 3px solid #cfcfcf; background-color: #cfcfcf; }

@media only screen and (max-width: 1023px) { .checkbox__icon { height: 40px; width: 40px; } }

@media only screen and (max-width: 599px) { .checkbox__icon { width: 20px; height: 20px; } }

.has-error .checkbox__icon { border-color: #ff0000; }

.checkbox__icon:before { content: ''; position: absolute; transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75); transform: scale(0) rotate(45deg) translate(-1px, -1px); top: 0; bottom: 0; left: 0; right: 0; margin: auto; z-index: 1; border-right: 2px solid #14b737; border-bottom: 2px solid #14b737; height: 15px; width: 12px; }

@media only screen and (max-width: 599px) { .checkbox__icon:before { height: 10px; width: 8px; } }

[data-modal] { position: fixed; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; visibility: hidden; min-width: 320px; display: flex; justify-content: center; align-items: center; transform: translate3d(0, 0, 0); overflow: auto; transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; background-color: rgba(0, 0, 0, 0.5); }

[data-modal].is-open { opacity: 1; visibility: visible; z-index: 100; }

[data-modal-container] { position: relative; z-index: 1; }

.modal__container { width: 100%; margin-top: 10.2vh; margin-bottom: auto; background-color: #71225d; color: #fff; text-align: center; }

.modal__container_error { max-width: 663px; padding: 79px 10px 87px; margin-top: 30vh; }

.modal__container_success { max-width: 791px; padding: 50px 0 25px; }

@media only screen and (max-width: 767px) { .modal__container { padding: 40px 15px; } }

.modal__title { padding-bottom: 3vh; font-family: "MyriadPro-Regular"; font-size: 43px; font-weight: 400; line-height: 1.2; text-transform: uppercase; }

@media only screen and (max-width: 1023px) { .modal__title { font-size: 30px; } }

.modal__logo { margin-bottom: 2vh; background-color: #1b1b1b; display: flex; align-items: center; justify-content: center; height: 14.82vh; min-height: 100px; }

.modal__text { font-weight: 400; }

@media only screen and (max-width: 1500px) { .modal__text { font-size: 20px; } }

.modal__descr { margin-bottom: 36px; padding: 0 10px; font-weight: 700; }

@media only screen and (max-width: 1023px) { .modal__descr { margin-bottom: 18px; } }

@media only screen and (max-width: 599px) { .modal__descr { margin-bottom: 8px; } }

.modal__descr-small { color: #fff; font-family: "MyriadPro-Regular"; font-size: 18px; font-weight: 400; line-height: 1.7; padding: 0 96px 20px; }

@media only screen and (max-width: 1023px) { .modal__descr-small { padding: 20px; font-size: 14px; font-weight: 400; line-height: 1.2; } }

.modal__wish { margin-bottom: 2.5vh; text-transform: uppercase; }

.btn { display: inline-block; padding: 36px 75px 28px; border: 2px solid #924e9d; background-image: linear-gradient(45deg, #4f2169 0%, #c31e41 100%); text-decoration: none; }

