.btn-cta { display: inline-block; position: relative; background-color: transparent; z-index: 5; background-color: #D6125B; border-radius: 52px; filter: drop-shadow(0 0 9px rgba(0, 0, 0, 0.7)); }

.no-touch .btn-cta:hover .btn-cta__shadow { opacity: 1; }

.btn-cta__shadow { position: absolute; top: 100%; left: 10%; right: 10%; box-shadow: 0 0 21px 16px rgba(0, 0, 0, 0.7); opacity: 0.5; transition: opacity 0.3s; }

