body {
	font-family: $circle;
	font-size: 24px;
	font-weight: 700;
	line-height: 1.2;
	@include md {
		font-size: 20px; }
	@include xs {
		font-size: 18px; } }

.main {
	height: 100vh;
	// height: 100%
	display: flex;
	flex-direction: column;
	background: url(../img/hero_money.png), url(../img/hero_anubis.png), url(../img/hero_gor.png), url(../img/hero_bg.png);
	background-size: auto calc(100% + 50px), auto calc(100% - 200px), auto calc(100% - 200px), cover;
	background-repeat: no-repeat;
	background-position: top -85px center, right calc(50% - 372px) center, left calc(50% - 372px) center, 0 0;
	@include md {
		background-size: auto calc(100% - 250px), auto calc(100% - 90px), auto calc(100% - 90px), cover;
		background-position: center top 53px, right top, left top, 0 0; }
	@include xsh {
		height: 100%; } }


.form-wrapper {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	padding: 52px 120px 110px;
	@include xl {
		padding: 40px 100px 80px; }
	@include md {
		padding: 188px 90px 78px 67px; }
	@include sm {
		padding: 130px 30px 30px; }
	@include xs {
		padding: 20vh 30px 10vh; } }

.text-upper-bold {
	@include f(24px, 700, 1.2);
	text-transform: uppercase; }
