.btn {
	display: inline-block;
	padding: 36px 75px 28px;
	border: 2px solid #924e9d;
	background-image: linear-gradient(45deg, #4f2169 0%, #c31e41 100%);
	text-decoration: none;
	@include md {
		padding: 18px 50px 14px; }
	&__text {
		color: $white;
		font-family: $circle;
		@include f(24px, 700, 1.2);
		text-transform: uppercase;
		@include xs {
			font-size: 18px; } } }

.btn-cta {
	display: inline-block;
	position: relative;
	background-color: transparent;
	z-index: 5;
	background-color: #D6125B;
	border-radius: 52px;
	filter: drop-shadow(0 0 9px rgba(0, 0, 0, 0.7));
	@include hover {
		.btn-cta__shadow {
			opacity: 1; } }
	&__shadow {
		position: absolute;
		top: 100%;
		left: 10%;
		right: 10%;
		box-shadow: 0 0 21px 16px rgba(0, 0, 0, 0.7);
		opacity: 0.5;
		transition: opacity $tr; }
	&__inner {
		position: relative;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 335px;
		height: 100px;
		background-image: url(../img/btn_active.png);
		background-size: cover;
		z-index: 5;
		text-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
		color: $white;
		font-family: $acrom;
		@include f(32px, 900, 1);
		text-transform: uppercase;
		letter-spacing: 0.02em;
		@include xl {
			width: 307px;
			height: 91px; }
		@include md {
			width: 290px;
			height: 87px;
			font-size: 28px; }
		@include xs {
			width: 154px;
			height: 46px;
			font-size: 24px; } } }

