.footer {
	padding: 43px 20px 16px;
	background-color: $black;
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 180px;
	@include xl {
		min-height: 0;
		padding: 16px 20px;
		align-items: flex-start; }
	@include sm {
		padding: 54px 20px; }
	@include xs {
		padding: 20px; }
	&__logo {
		@include flex-size(24.4%);
		text-align: center;
		color: $white;
		font-family: $myriad;
		@include f(86px, 400, 1);
		@include xl {
			@include flex-size(20%);
			font-size: 50px; }
		@include md {
			@include flex-size(100%);
			font-size: 86px; }
		@include xs {
			font-size: 40px; }
		&_right {
			@include xl {
				display: none; } } }
	&__center {
		flex-grow: 1;
		padding: 0 20px;
		text-align: center;
		@include md {
			display: none; } }
	&__partners,
	&__payments {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		margin: 0 -6px 16px;
		@include xl {
			margin-bottom: 10px; } }
	&__item {
		padding: 0 6px;
		font-size: 0;
		& > * {
			display: inline-block; } }
	&__icon {
		text-align: center;
		.icon-min_age {
			display: inline-block; } } }

